.menuWrap {
  display: flex;
  justify-content: center;
  padding-top: 110px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #f8f8f7;
  & button:nth-child(1) {
    position: absolute;
    top: 30px;
    right: 30px;
  }
}

.navList {
  background-color: teal;
}

.searchInputLabel {
  position: relative;
}

.inputSearch {
  width: 335px;
  @media screen and (min-width: 768px) {
    width: 430px;
  }
  @media screen and (min-width: 1440px) {
    width: 470px;
  }
  height: 60px;
  background: #ffffff;
  border: none;
  border-radius: 20px;
  padding: 15px 10px 15px 20px;
  font-family: 'Jost';
  font-weight: 400;
  font-size: 20px;
  line-height: calc(30 / 20);
  color: #1d1d1d;
}

.inputSearch:focus {
  outline: none;
  border: 2px solid #ff868e;
}

.inputSearch::-webkit-search-decoration,
.inputSearch::-webkit-search-cancel-button,
.inputSearch::-webkit-search-results-button,
.inputSearch::-webkit-search-results-decoration {
  display: none;
}

.inputSearch::placeholder {
  font-family: 'Jost';
  font-weight: 400;
  font-size: 20px;
  line-height: calc(30 / 20);
  color: #8c8c8c;
}

.submitButton {
  position: absolute;
  top: -10px;
  right: 10px;
  width: 40px;
  height: 40px;
  border: none;
  background-color: #fbe0dc;
  background-image: url(./images/icons/search.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 20px;
  background-position: center;
  border-radius: 10px;
  transition: 200ms;
}

.submitButton:hover {
  cursor: pointer;
  background-image: url(./images/icons/search-hover.svg);
  background-color: #ff868e;
}

.UserCollections {
  display: flex;
  gap: 10px;
}

.categoryTopWrap {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  justify-content: space-between;
}

.categoryBottomWrap {
  position: relative;
  background: #ffffff;
  border-radius: 20px;
  padding: 20px;
  min-height: 570px;
}
